import React from 'react';

let backGro ={
  backgroundColor: "white",
  width: "100%",
  height: "100vh",
  margin: "auto auto",
  textAlign: "center",
  fontSize: "46px"
}

function Logo() {
  return (
    <div style={backGro}>
      Application Logo
    </div>
  )
}

export default Logo
